.field-checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  /* checked mark aspect */
}
.field-checkbox:not(:checked) + label,
.field-checkbox:checked + label {
  position: relative;
  display: block;
  cursor: pointer;
}
.field-checkbox:not(:checked) + label > span,
.field-checkbox:checked + label > span {
  display: block;
  margin-left: 30px;
  font-size: 14px;
}
@media (min-width: 768px) {
  .field-checkbox:not(:checked) + label > span,
  .field-checkbox:checked + label > span {
    margin-left: 24px;
  }
}
@media (min-width: 1280px) {
  .field-checkbox:not(:checked) + label > span,
  .field-checkbox:checked + label > span {
    font-size: 17px;
  }
}
.field-checkbox:not(:checked) + label:before,
.field-checkbox:checked + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #788d98;
  background: transparent;
  border-radius: 50%;
  font-size: 14px;
}
@media (min-width: 768px) {
  .field-checkbox:not(:checked) + label:before,
  .field-checkbox:checked + label:before {
    top: -2px;
  }
}
@media (min-width: 768px) {
  .field-checkbox:not(:checked) + label:before,
  .field-checkbox:checked + label:before {
    top: 0;
  }
}
.field-checkbox:not(:checked) + label:after,
.field-checkbox:checked + label:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #e01d42;
  transition: all .2s;
}
@media (min-width: 768px) {
  .field-checkbox:not(:checked) + label:after,
  .field-checkbox:checked + label:after {
    top: 5px;
  }
}
@media (min-width: 1280px) {
  .field-checkbox:not(:checked) + label:after,
  .field-checkbox:checked + label:after {
    top: 5px;
  }
}
.field-checkbox.error + label:before {
  border: 2px solid #e01d42;
}
.field-checkbox:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
}
.field-checkbox:checked + label:after {
  opacity: 1;
  transform: scale(1);
}
